.footer {
  padding-top: 60px;
  padding-bottom: 60px;
  width: 100%;
  background-color: $dark-bg;
  border: 1px solid red;
  position: relative;
  display: flex;
  align-items: center;
  // background-color: #D4FFE9;

    &::after {
    display: block;
    content: '';
    position: absolute;
    left: 0;
    bottom: 90px;
    width: 100%;
    height: 1px;
    background-color: #e1e1e1;
  }
}

// .footer-container {
//   border: 1px solid blue;
//   @include container();
// }


.footer-contact-part {
  border: 1px solid green;
  @media screen and(min-width: 768px) {
    display: flex;
    justify-content: space-around;
    align-items: baseline;
    // margin-bottom: 120px;
  }
}

@media screen and(min-width: 1200px) {
  // .footer {
  //   display: flex;
  //   justify-content: space-evenly;
  //   align-items: baseline;
  // }

  @media screen and (min-width: 1200px) {
    .footer {
      .container {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
      }
    }

    .footer-widget {
      margin-right: 70px;
    }
  }
}


.footer-widget {
  margin-bottom: 60px;
  text-align: center;

  @include for-size(desktop) {
    margin-bottom: 0;
  }
}

.logo-svg {
  width: 145px;
  height: 100px;
  // border: 1px solid pink;
}


