.footer-contacts-link:not(:last-child) {
  margin-bottom: 8px;
  // border: 1px solid white
}

.footer-contacts {
  // @include fonts(14px, 1.71);
  font-size: 18px;
  line-height: 1.71;
  color: rgba(255, 255, 255, 0.6);
}

.social-link-footer {
  @include centered(flex);
  border-radius: 50%;
  @include size(44px, 44px);
  background: rgba(255, 255, 255, 0.1);
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    background-color: $accent-cl;
  }
}

.social-icon-footer {
  @include size(20px, 20px);
  fill: $white;
}

.adress {
  color: $white;
  @include fonts(14px, 1.71);
}

.footer-links {
  text-align: center;
  margin-bottom: 60px;

  @include for-size(desktop) {
    margin-bottom: 0;
  }
}

.join {
  margin-bottom: 20px;
  @include fonts(14px, 700, 1.14, 0.03em);
  text-transform: uppercase;
  color: $white;

  @include for-size(desktop) {
    text-align: left;
  }
}

.footer-contacts-link:not(:last-child) {
  margin-bottom: 8px;
}

.footer-contacts {
  @include fonts(14px, 1.71);
  font-size: 14px;
  line-height: 1.71;
  color: rgba(255, 255, 255, 0.6);
}




